export default {
  state: {
    date: '',
    weekdays: []
  },
  mutations: {
    SET_DATE(state, date) {
      state.date = date
    },
    SET_WEEKDAYS(state, startDate) {
      function formatDate (date) {
        let day = date.getDate()
        let month = date.getMonth() + 1
        let year = date.getFullYear()
        if (day.toString().length < 2)
          day = '0' + day.toString()
        if (month.toString().length < 2)
          month = '0' + month.toString()
        return `${year.toString()}.${month}.${day}`
      }
      let weekdays = []
      for (let i = 0; i < 8; i++) {
        const nextDay = new Date(startDate)
        nextDay.setDate(nextDay.getDate() + i)
        let day, date, month, weekdayObj
        switch (nextDay.getDay()) {
          case 1:
            day = ''
            break;
          case 2:
            day = ''
            break;
          case 3:
            day = ''
            break;
          case 4:
            day = ''
            break;
          case 5:
            day = ''
            break;
          case 6:
            day = ''
            break;
          case 0:
            day = ''
            break;
          default:
            break;
        }
        date = nextDay.getDate()
        switch (nextDay.getMonth()) {
          case 0:
            month = ''
            break;
          case 1:
            month = ''
            break;
          case 2:
            month = ''
            break;
          case 3:
            month = ''
            break;
          case 4:
            month = ''
            break;
          case 5:
            month = ''
            break;
          case 6:
            month = ''
            break;
          case 7:
            month = ''
            break;
          case 8:
            month = ''
            break;
          case 9:
            month = ''
            break;
          case 10:
            month = ''
            break;
          case 11:
            month = ''
            break;
          default:
            break;
        }
        weekdayObj = { day: day, date: date, month: month, fullDate: formatDate(nextDay) }
        weekdays.push(weekdayObj)
      }
      state.weekdays = weekdays
    }
  },
  actions: {
    SET_DATE({commit}, date) {
      commit('SET_DATE', date)
    },
    SET_WEEKDAYS({commit}, startDate) {
      commit('SET_WEEKDAYS', startDate)
    }
  },
  getters: {
    date: (state) => state.date,
    weekdays: (state) => state.weekdays
  }
}